@import url(https://fonts.googleapis.com/css2?family=Yaldevi:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.App{display:flex;min-height:100vh;flex-direction:column;align-items:center;justify-content:center}.App-header{font-size:calc(10px + 2vmin);color:grey}.App-header h1{font-family:'Yaldevi', sans-serif;color:#657f9f}.App-link{color:#61dafb}

.search-results{display:flex;flex-wrap:wrap;justify-content:space-between;width:80vw;margin:20px 0}.search-results article{padding-left:20px;width:33%;font-family:'Roboto', sans-serif;box-shadow:5px 5px 5px -5px rgba(34,60,80,0.6);box-sizing:border-box}@media screen and (max-width: 728px){.search-results article{width:100%}}.search-results article:hover a h1{font-weight:bold}.search-results article a{color:#000;text-decoration:none}.search-results article a h1{font-weight:normal;word-break:break-all}@media screen and (min-width: 729px){.search-results article:nth-child(3n+3){padding-right:20px;text-align:end;box-shadow:-5px 5px 5px -5px rgba(34,60,80,0.6)}.search-results article:nth-child(3n+2){text-align:center;box-shadow:0px 5px 5px -5px rgba(34,60,80,0.6)}}.search-results article:hover{background-color:rgba(34,60,80,0.1)}

.App-form{width:30vw;position:relative}.App-form #search{border:none;padding-left:10%;background:#f0f8ff;height:25px;width:90%;border-radius:5px;box-shadow:0px 5px 10px 2px rgba(34,60,80,0.2)}.App-form #reset-icon-button{position:absolute;top:0%;height:100%;width:25px;right:0;background:none;border:none}

